import {useHttp} from '../../hooks/http.hook';
import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import { heroesFetched, fetchHeroes } from '../../reducers/heroesSlice';
import HeroesListItem from "../heroesListItem/HeroesListItem";
import Spinner from '../spinner/Spinner';

//    :
//    ""      
//  :
//     json     DELETE

const HeroesList = () => {
    const {heroes, heroesLoadingStatus, activID} = useSelector(state => state.heroes);
    const {activFilter} = useSelector(state => state.filters);
    const dispatch = useDispatch();
    const {request} = useHttp();

    useEffect(() => {
        dispatch(fetchHeroes());
    }, []);

    useEffect(() => {
        const newHeroes = heroes.filter((item) => item.id !== activID);
        dispatch(heroesFetched(newHeroes));
        if (activID !== '') {
            request(`http://localhost:3001/heroes/${activID}`, 'DELETE')
        }
    }, [activID])

    if (heroesLoadingStatus === "loading") {
        return <Spinner/>;
    } else if (heroesLoadingStatus === "error") {
        return <h5 className="text-center mt-5"> </h5>
    }

    const renderHeroesList = (arr) => {

        if (arr.length === 0) {
            return <h5 className="text-center mt-5">  </h5>
        }

        return arr.map(({id, ...props}) => {
            return <HeroesListItem key={id} id={id} {...props}/>
        })
    }

    let elements;

    if (activFilter === 'all') {
        elements = renderHeroesList(heroes);
    } else {
        const filterHeroes = heroes.filter(item => item.element === activFilter);
        elements = renderHeroesList(filterHeroes);
    }

    // const elements = renderHeroesList(heroes);
    return (
        <ul>
            {elements}
        </ul>
    )
}

export default HeroesList;