import { useState } from "react";
import { useForm, Controller } from "react-hook-form";
import Select from "react-select";
import { v4 as uuidv4 } from 'uuid';
import { useDispatch } from 'react-redux';

import { fetchHeroes, heroesFetching } from '../../reducers/heroesSlice';
import { useHttp } from "../../hooks/http.hook";

const HeroesAddForm = () => {
    const { register, handleSubmit, control, reset } = useForm();
    const [newItem, setNewItem] = useState({});
    const {request} = useHttp();
    const dispatch = useDispatch();

    const onSubmit = (data) => {
        const newHero = {
            id: uuidv4(),
            name: data.name,
            description: data.text,
            element: data.element.value 
        };

        setNewItem(newHero);

        dispatch(heroesFetching());
        request("http://localhost:3001/heroes", "POST", JSON.stringify(newHero));
        dispatch(fetchHeroes());

        reset();
    };

    return (
        <form 
            className="border p-4 shadow-lg rounded" 
            autoComplete="off" 
            onSubmit={handleSubmit(onSubmit)}>
                <div className="mb-3">
                    <label htmlFor="name" className="form-label fs-4">  </label>
                    <input 
                        className="form-control"
                        type="text"
                        id='name'
                        placeholder="  ?"
                        {...register("name", { required: true })}
                    />  
                </div>

                <div className="mb-3">
                    <label htmlFor="text" className="form-label fs-4"></label>
                    <textarea 
                        className="form-control"
                        id='text'
                        type="text" 
                        placeholder="  ?"
                        style={{"height": '130px'}}
                        {...register("text", { required: true })}
                    />
                </div>

                <div className="mb-3">
                    <label htmlFor="element" className="form-label">  </label>
                    <Controller 
                        label="element"
                        name="element"
                        control={control}
                        defaultValue={null}
                        render={({ field }) => (
                        <Select
                            required
                            {...field}
                            placeholder="  ..."
                            options={[
                                { value: "fire", label: "" },
                                { value: "water", label: "" },
                                { value: "wind", label: "" },
                                { value: "earth", label: "" },
                            ]}
                        />
                        )}
                    />
                </div>
                <button type="submit" className="btn btn-primary"></button>
        </form>
    )
}

export default HeroesAddForm;